<template>
  <div ref="wrapper">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
import bscroll from 'better-scroll'

export default {
  el: '',
  data () {
    return {
      scroll:null,
      msg:'qiaoyang'
    }
  },
  props:{
    probetype:{   //0123
      type:Number,
      default:3
    },
    pullupload:{    //,
      type:Boolean,
      default:true
    },
    click:{  
      type:Boolean,
      default:true
    }
  },
  methods: {

  },
  mounted(){
    this.scroll = new bscroll(this.$refs.wrapper,{      //$ref
      probeType:this.probetype,
      pullUpLoad:this.pullupload,
      click:this.click,
      observeDOM:true        //2.0DOM
    })
    this.scroll.on('scroll',(position)=>{
      this.$emit('getPosition',position)
      
    }) 
    this.scroll.on('pullingUp',()=>{     //pullingUp
      this.$emit('pullingUp')
      
      this.scroll.finishPullUp()     //finishPullUp
    })
    
  }
}
</script>

